___CSS_LOADER_EXPORT___.push([module.id, ".splide__arrow.splide__arrow--next,.splide__arrow.splide__arrow--prev{display:none}", "",{"version":3,"sources":["webpack://./resources/js/Components/Guru.vue"],"names":[],"mappings":"AA2IE,sEAAA,YAAa","sourcesContent":["<template>\r\n  <div class=\"container mx-auto pt-20 relative px-5\">\r\n    <div class=\"flex flex-col items-center\">\r\n      <h1 class=\"text-32 font-bold\">{{ judulguru }}</h1>\r\n      <p class=\"text-[#5A5A5A] text-center\">{{ titleguru }}</p>\r\n    </div>\r\n    <splide :options=\"options\" class=\"relative\" @moved=\"onSlideMove\">\r\n      <splide-slide class=\"py-20\" v-for=\"(item, index) in visibleguru\" :key=\"index\">\r\n        <div :class=\"slideClass(index)\" class=\"container mx-auto flex flex-col justify-center\">\r\n          <img :src=\"item.imguru\" alt=\"\" />\r\n          <p class=\"text-center\">{{ item.titlegambarguru }}</p>\r\n          <p class=\"text-center\">{{ item.subtitleguru }}</p>\r\n        </div>\r\n      </splide-slide>\r\n      <div class=\"panah absolute flex justify-between w-full top-40\">\r\n        <button @click=\"prevSlide\" :disabled=\"!canGoPrev\">\r\n          <img class=\"w-12 h-12\" :src=\"icon.img1\" alt=\"\" />\r\n        </button>\r\n        <button @click=\"nextSlide\" :disabled=\"!canGoNext\">\r\n          <img class=\"w-12 h-12\" :src=\"icon.img\" alt=\"\" />\r\n        </button>\r\n      </div>\r\n    </splide>\r\n  </div>\r\n</template>\r\n\r\n<script>\r\nimport \"@splidejs/splide/dist/css/themes/splide-default.min.css\";\r\nimport { Splide, SplideSlide } from \"@splidejs/vue-splide\";\r\nexport default {\r\n  props: [\"judulguru\", \"titleguru\", \"visibleguru\"],\r\n  data() {\r\n    return {\r\n      activeSlideIndex: 0,\r\n      options: {\r\n        rewind: true,\r\n        gap: \"1rem\",\r\n        perPage: 5,\r\n      },\r\n      icon: {\r\n        img: this.$settings.imagesPublic + \"/bx-chevron-right-circle.svg\",\r\n        img1: this.$settings.imagesPublic + \"/bx-chevron-left-circle.svg\",\r\n      },\r\n\r\n      judul: {\r\n        title: \"Guru - Guru Kami\",\r\n        sub: \"Lörem ipsum mögunde seråpp pos. Faplasade jer och titeck \",\r\n      },\r\n      guru: [\r\n        {\r\n          image: this.$settings.imagesPublic + \"/guru1.png\",\r\n          title: \"Iwan S.Pd\",\r\n          sub: \"Kepala Sekolah\",\r\n        },\r\n        {\r\n          image: this.$settings.imagesPublic + \"/guru1.png\",\r\n          title: \"Iwan S.Pd\",\r\n          sub: \"Kepala Sekolah\",\r\n        },\r\n        {\r\n          image: this.$settings.imagesPublic + \"/guru1.png\",\r\n          title: \"Iwan S.Pd\",\r\n          sub: \"Kepala Sekolah\",\r\n        },\r\n        {\r\n          image: this.$settings.imagesPublic + \"/guru1.png\",\r\n          title: \"Iwan S.Pd\",\r\n          sub: \"Kepala Sekolah\",\r\n        },\r\n        {\r\n          image: this.$settings.imagesPublic + \"/guru1.png\",\r\n          title: \"Iwan S.Pd\",\r\n          sub: \"Kepala Sekolah\",\r\n        },\r\n        {\r\n          image: this.$settings.imagesPublic + \"/guru1.png\",\r\n          title: \"Iwan S.Pd\",\r\n          sub: \"Kepala Sekolah\",\r\n        },\r\n      ],\r\n    };\r\n  },\r\n  components: {\r\n    Splide,\r\n    SplideSlide,\r\n  },\r\n\r\n  computed: {\r\n    // Check if it's possible to go to the previous slide\r\n    canGoPrev() {\r\n      return this.activeSlideIndex > 0;\r\n    },\r\n\r\n    // Check if it's possible to go to the next slide\r\n    canGoNext() {\r\n      return this.activeSlideIndex < this.guru.length - this.options.perPage;\r\n    },\r\n\r\n    // Get the visible slides based on the current active slide index\r\n    visibleGuru() {\r\n      const firstVisibleIndex = Math.max(0, this.activeSlideIndex - Math.floor(this.options.perPage / 2));\r\n      const lastVisibleIndex = Math.min(this.guru.length - 1, firstVisibleIndex + this.options.perPage - 1);\r\n      return this.guru.slice(firstVisibleIndex, lastVisibleIndex + 1);\r\n    },\r\n  },\r\n\r\n  methods: {\r\n    // Method to change to the previous slide\r\n    prevSlide() {\r\n      if (this.canGoPrev) {\r\n        this.activeSlideIndex--;\r\n      }\r\n    },\r\n    // Method to change to the next slide\r\n    nextSlide() {\r\n      if (this.canGoNext) {\r\n        this.activeSlideIndex++;\r\n      }\r\n    },\r\n    // Method to apply opacity based on the active slide\r\n    slideClass(index) {\r\n      const distanceFromCenter = Math.abs(index - this.activeSlideIndex);\r\n      return {\r\n        \"opacity-75\": distanceFromCenter !== 0,\r\n        \"opacity-100\": distanceFromCenter === 0,\r\n      };\r\n    },\r\n    // When slides are moved (for splide moved event)\r\n    onSlideMove() {\r\n      // Updating the active slide index based on the slide moved event\r\n      this.activeSlideIndex = this.$refs.splide.index;\r\n    },\r\n  },\r\n};\r\n</script>\r\n\r\n<style>\r\n.splide__arrow.splide__arrow--prev,\r\n.splide__arrow.splide__arrow--next {\r\n  @apply hidden;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
